import React, {Component} from 'react'
import one from '../images/1.png'
import two from '../images/2.png'


class CanvasActual extends Component {
	state={
		currentImage: one
	}

  render() {
    return (
	<div id="canvasWeb" >
	<h1 style={{position: "absolute", top: "5px"}}>Canvas Web</h1>
	<div id="beginCanvasActual" style={{marginTop: "75px"}}>
		<div id="canvasBlackOuter" style={{width: "30em", height: "22em", border: "15px solid black", background: "beige"}}>
		<div id="currentImage" />
		</div>
		</div>
	</div>
    )
  }
}
export default CanvasActual



