import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import CanvasActual from './CanvasActual'


import one from '../images/1.png'
import two from '../images/2.png'
import three from '../images/3.png'
import four from '../images/4.png'
import five from '../images/5.png'


class CanvasWeb extends Component {
	state={
		images: [
			one,
			two,
			three,
			four,
			five
		]
	}

handleClick = image => () => {
	const element = (
				<img style={{padding: "30px", width: "100%", height: "100%"}} src={image.image} alt="Current Image"/>
			);
			ReactDOM.render(element, document.getElementById('currentImage'))
}

componentDidMount() {
	const element = (
				<img style={{padding: "30px", width: "100%", height: "100%"}} src={one} alt="Current Image"/>
			);
			ReactDOM.render(element, document.getElementById('currentImage'))
    }

  render() {

		const images = this.state.images.map(image => {
		return(
			<img class="w3-round-large w3-margin" style={{width: "90%", boxShadow: "0 4px 8px 3px rgba(0, 0, 0, 0.2)", cursor: "pointer"}} key={image} src={image} alt={image} onClick={this.handleClick({image})} />
    )})

    return (

	<div id="canvasWeb" >
	<h1 style={{position: "absolute", top: "5px"}}>Canvas Web</h1>
	<div id="beginCanvasWeb" style={{backgroundColor:"#f2f2f2", overflowY: "auto", width: "30em", height: "90vh", marginTop: "75px"}}>
		<div id="navbar" align="center" style={{padding: "5px", height: "30px", backgroundColor: "#456096", color: "white"}}>Canvas</div>
		<div id="uploadBar" class="w3-round-large w3-card" style={{paddingTop: "10px", paddingBottom: "10px", backgroundColor: "#fcfcfc", marginTop: "40px", marginBottom: "10px", marginLeft: "5px", marginRight: "5px", boxShadow: "0 4px 8px 3px rgba(0, 0, 0, 0.2)", textAlign: "center"}}>
		<input type="file" class="w3-center w3-button  w3-round-xlarge file-btn" style={{textAlign: "center", color: "white", backgroundColor: "#456096", margin:"1px", marginLeft: "5px", marginTop: "2px", display: "inline"}}  id="upload"  />

		<div style={{paddingTop: "15px"}}>
			<input id="resetCAROUSEL" class="w3-button w3-round-xlarge" style={{textAlign: "center", color: "white", backgroundColor: "#456096", margin: "1px", marginTop: "0px"}} value="Reset Carousel"/>
			<button id="showEditButton" class="w3-button  w3-round-xlarge" style={{textAlign: "center", color: "white", backgroundColor: "#456096", display: "inline", marginTop: "0px", marginRight: "10px", width: "50px"}} onclick="openEdit()"><i class="fa fa-cogs"></i></button>
		</div>
	</div>

		{images}

	</div>
	</div>
    )
  }
}

export default CanvasWeb



