import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import CanvasWeb from './components/CanvasWeb'
import CanvasActual from './components/CanvasActual'

class App extends Component {
  render() {
    return (
  <div style={{textAlign: "-webkit-center"}} className="container">
		<table>
			<tr>
				<td style={{width: "50%"}}>
		      <CanvasWeb />
				</td>
				<td style={{width: "50%"}}>
					<CanvasActual />
				</td>
			</tr>
		</table>
	      </div>
    )
  }
}

export default App



